<template>
  <div>
    <div id="map" class="map"></div>
    <a id="back-home" class="back-button" v-if="backBtn" @click="back">
      返回
    </a>
  </div>
</template>

<script>

import axios from "axios";
//世界数据
import compJson from "../assets/comp.json"
//中国数据
import chinaJson from "../assets/china.json"
// import convertMapData from "../utils/convertMapData"


export default {
  name: "index",
  data() {
    return {
      map: {},
      backBtn: false,
      //存放栈信息
      mapHistory:[],
      //目前属于第几层，最顶层为0，下钻一层加1
      length: 0,
      //目前最多可以下钻到第几层
      maxLength: 1,
      //要展示的数据,地图的和气泡的信息
      // PointJson:compJson.array,
      //存放气泡信息
      scatterData: [],
      compJsonArray : compJson.array,
      compJsonDisplay : compJson.display
    };
  },
  created(){
    this.getWorldJson()
    this.mapHistory.push()
    this.scatterDataSet(compJson.array)
  },
  mounted(){
  },
  watch:{
    //普通监听，还有深度监听
    length(val,oldVal){
      if(val == 0){
        this.backBtn = false
      }
      if(val == 1){

      }
    }
  },
  methods: {
    async getWorldJson(){
      //axios获取本地的Json是放在public的
      //获得世界地图数据
      let {data:geoJson} = await axios.get('./countries2.geo.json')
      // let {data:mapTopJson} = await axios.get('./mapTop.json')
      this.worldMap(geoJson)
    },
    worldMap(geoJson){

      var compJsonDisplay = this.compJsonDisplay

      this.$echarts.registerMap("world", geoJson);
      let myChart = this.$echarts.init(document.getElementById("map"));
      let setOption = {
          title: {
            subtext: "",
            left: "center",
            top: "3%",
            textStyle: {          
              color: "#d5e7f3",
              fontSize: 18,
              fontFamily:'微软雅黑',
              fontWeight:'bold'
            },
          },
          tooltip: {
            trigger:"item",
            formatter: function(params) {
              //根据params.seriesIndex判断数据是来自map的还是scatter的
              if(params.seriesIndex==0)
                return params.name + ":" +params.data.value[0]
              else
                return params.name + ":" +params.data.value[2] 
            },
          },
          geo: {
            map: "world",
            show: true,
            //是否开启鼠标缩放和平移漫游
            roam: false,
            left: "center",
            label:{
              show: true,
              color: "#404040",
              // color:"#fff",
              fontSize:'oblique',
              fontWeight: 'bolder',
              formatter(params){
                //这个label展示的是地图上显示的名字
                if(compJsonDisplay[params.name]){
                   return params.name
                }
                else{
                    return ''
                }
                   
              }
            },
            
            // 设置放大比例
            zoom:1.2,
          },
          series: [
            //第一个数据（地图）
            {
              type: "map",
              map: "world",
              //和geo绑定
              geoIndex: 0,
              //compJson
              data: this.compJsonArray
            },
            //第二个数据：气泡
            {
              name: "pin",
              type: "scatter",
              coordinateSystem: "geo",
              symbol: "pin",
              symbolSize: [50, 50],
              label: {
                normal: {
                  show: true,
                  textStyle: {
                    color: "#fff",
                    fontSize: 9,
                  },
                  //气泡的内容，不是提示框！
                  formatter(value) {
                    return value.data.value[2];
                  },
                },
              },
              itemStyle: {
                normal: {
                  // color: "#ffcc00",
                  color:'#7476e0'
                },
              },
              data : this.scatterData,
              showEffectOn: "render",
              rippleEffect: {
                brushType: "stroke",
              },
              hoverAnimation: true,
              zlevel: 1,
            },

          ],
          visualMap:{
            //只绑定第一个series
            seriesIndex: 0,
            min: 0,
            max: 1200,
            calculable: true,  
            color: ["red","darkorange", "gold", "lawngreen", "lightseagreen","white"] 
          }
      }


      myChart.setOption(setOption,true);
      window.addEventListener("resize", function() {
        myChart.resize();
      });


      if(this.length==0){
        

        setOption.series[1].data = this.scatterData
        setOption.series[0].data = this.compJsonArray
        myChart.resize();
      }

      myChart.on('click',async(data)=>{
        let name
        name = data.name
            for(let i = 0;i<this.compJsonArray.length;i++){
            //如果进行下钻，还需要下一级
              if(name == this.compJsonArray[i].name){
                //保存Json地址
                var newI = this.compJsonArray[i].json
                //保存最终的json地址
                var newAddr = `./countries/`+newI+`.geo.json`
                //导入相对应的json
                let {data:newJson} = await axios.get(newAddr)
                
                //进入下一级前先检查是否已经到底了，如果没到底就Push,并且length+1
                if(this.length<this.maxLength){
                  this.mapHistory.push(name)
                  this.length+=1

                  if(this.$route.query.country === data.name){
                    this.$router.push({ path: "/" });
                  }
                  else{
                    this.$router.push({path:"/country",query:{country:data.name}})
                  }
                }
                this.$echarts.registerMap("world", newJson);

                //暂时只有中国的数据，所以加这个判断
                if(this.compJsonArray[i].data!=undefined){
                  this.scatterDataSet(this.compJsonArray[i].data)
                  setOption.series[1].data = this.scatterData
                  setOption.series[0].data = this.compJsonArray[i].data
                }
                else{
                  setOption.series[1].data = []

                }
                
                myChart.setOption(setOption);
                //成功找到后始终保持返回按钮显示
                this.backBtn = true 
                document.querySelector('.map').style.width = '60%'
                myChart.resize();           

                break
              }
          }
      })


    },
    async back(){
      
      //判断长度
      if(this.length>0){
        //先踢出去一个
        this.mapHistory.pop()
        this.length --  
        this.scatterDataSet(this.compJsonArray)
      }
      
      if(this.mapHistory.length < 1){
        this.getWorldJson()
        document.querySelector('.map').style.width = '100%'
        this.$router.push({path:'/'})
        document.querySelector('body').style.backgroundColor = '#99bbff'
      }
      else{
        let historyCityName = this.mapHistory[this.mapHistory.length -1]
        let { data:MapJson } = await axios.get(`./countries/`+historyCityName+`.geo.json`)
        this.worldMap(MapJson)
      }
    },
    async scatterDataSet(dataJson){
      var data = []
      
      for(var i = 0;i<dataJson.length;i++){
        if(dataJson[i].center!=undefined){
          var newObject={
            name: '',
            value:[]
          }
          newObject.name = dataJson[i].name
          newObject.value[2] = dataJson[i].value
          newObject.value[0] = dataJson[i].center[0]
          newObject.value[1] = dataJson[i].center[1]
          data.push(newObject)
          this.scatterData = data
        }
      }
      // return data
    }
  },
};
</script>

<style scoped>
@import "../styles/map.css";
</style>
