<template>
    <div>
        <recruitment-board id="recruitmentBoard" :countriesData="countriesData"></recruitment-board>
    </div>
</template>
<script>

import RecruitmentBoard from "../components/recruitmentBoard.vue";
import countriesData from "../assets/countriesData.json"

export default{
    name: "index",
    components:{
        RecruitmentBoard
    },
    data() {
        return {
            countriesData : []
        }
    },
    watch:{

    },
    async created(){
        let country = this.$route.query.country
        if(country == "United States"){
            country = 'USA'
        }
        else if(country == 'United Kingdom'){
            country = 'UK'
        }
        if(countriesData.data[country]!=undefined){
            this.countriesData = countriesData.data[country]
        }
        else{
            var newObject = {
                "name": "",
                "contact": "王先生",
                "phone": "1822130866",
                "email": "@163.com",
                "project_name": "电池及电池材料研究"
            }
            countriesData.data.else = []
            newObject.name = country
            for(let i = 0;i<30;i++){
                countriesData.data.else.push(newObject)
            }
            this.countriesData = countriesData.data.else
        }
        

    }
}
</script>
<style scoped>
#recruitmentBoard{
    position: absolute;
    right: 3%;
    bottom: 32%;
}
</style>